.App {
  background: #25282b;
  color: white;
  text-align: center;
  font-size: 3vmin;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Game */

ol, ul {
  list-style: none;
  padding-left: 0px;
  display: inline-grid;
  grid-template-rows: repeat(4, auto);
  grid-auto-flow: column;
}

.square {
  border: 1px solid #999;
  float: left;
  font-size: 5vmin;
  font-weight: bold;
  line-height: 10vmin;
  height: 10vmin;
  width: 10vmin;
}

.square:hover {
  border: 1px solid rgb(45, 160, 80);
}

.game {
  height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 10vmin;
}

.move {
  border: 1px solid #999;
  cursor: default;
  margin: 1vmin;
  padding: 1vmin;
}

.move:hover {
  border: 1px solid rgb(39, 133, 170);
}
